import {connect} from "react-redux";
import ResultComponent from "./ResultComponent";
import React, {Fragment, useContext, useEffect, useState} from "react";
import {getAllAnswers} from "../../../redux/reducers/question-reducer";
import {Card, CardBody, CardFooter, CardHeader, CardText, Col, CustomInput, Table} from "reactstrap";
import {Check, CheckSquare, Clock, Codesandbox, FileText, Image, Sliders, Star, UserCheck, X, XSquare} from "react-feather";
import s from '../testing/Testing.module.css';
import Rating from "react-rating";
import {ThemeColors} from "../../../utility/context/ThemeColors";
import {saveReview} from "../../../redux/reducers/profile-reducer";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";
const swal = withReactContent(Swal);

const ResultContainer = (props) => {
    useEffect(() => {
        if (props.userAnswers.length === 0) props.getAllAnswers(props.userId, props.history);
    }, []);
    const themeColors = useContext(ThemeColors);
    const iconSize = 50;
    const reviewIcons = [
        <FileText size={iconSize} />,
        <Image size={iconSize} />,
        <Clock size={iconSize} />,
        <Codesandbox size={iconSize} />,
        <Sliders size={iconSize} />,
        <UserCheck size={iconSize} />
    ]
    const review = [
        {
            text: 'Наполнение сайта полезной информацией',
            type: 'review_information',
            score: 0
        },
        {
            text: 'Оформление, или дизайн сайта',
            type: 'review_design',
            score: 0
        },
        {
            text: 'Скорость загрузки сайта и кабинета',
            type: 'review_speed',
            score: 0
        },
        {
            text: 'Система тестирования сайта',
            type: 'review_system',
            score: 0
        },
        {
            text: 'Структура сайта',
            type: 'review_structure',
            score: 0
        },
        {
            text: 'Собираетесь ли поступать в наш институт?',
            type: 'review_university',
            score: false
        }
    ]
    const setScore = (key, score) => {
        review[key].score = score;
    }
    const filledColor = themeColors.colors.warning.main;
    const Label = () => (
        <Fragment>
            <span className='switch-icon-left'>
              <Check size={14} />
            </span>
            <span className='switch-icon-right'>
              <X size={14} />
            </span>
        </Fragment>
    );

    const changeSwitch = (key) => {
        const score = review[key].score;
        setScore(key, !score);
    }

    const saveReview = () => {
        let error = 0;
        for (const index in review) {
            if (review[index].type !== 'review_university' && review[index].score === 0) {
                swal.fire({
                    title: "Ошибка!",
                    text: 'Все категории должны быть отмечены',
                    icon: "error",
                    confirmButtonText: "OK"
                });
                error = 1;
                break;
            }
        }
        if (error === 0) props.saveReview(review, props.userId);
    }
    const renderReview = review.map((item, index) => (
       <Col className={'pb-2'} key={index} md={4} xs={12}>
           <Card className={'full-height'}>
               <CardHeader>
                   {reviewIcons[index]}
               </CardHeader>
               <CardBody>
                   <CardText>
                       {item.text}
                   </CardText>
               </CardBody>
               <CardFooter>
                   {item.type !== 'review_university' ? <Rating
                       emptySymbol={<Star size={32} fill='#babfc7' stroke='#babfc7' />}
                       fullSymbol={<Star size={32} fill={filledColor} stroke={filledColor} />}
                       initialRating={item.score}
                       onChange={(e) => setScore(index, e)}
                       direction={'ltr'}
                   /> : <CustomInput
                       type='switch'
                       label={<Label />}
                       className='custom-control-success'
                       id='icon-success'
                       name='icon-success'
                       inline
                       onChange={() => changeSwitch(index)}
                   />}
               </CardFooter>
           </Card>
       </Col>
    ));

    const data = props.userAnswers
        .map((item, index) => (
            {
                title: `Вопрос №${index + 1}:`,
                text: item.question_text,
                content: (
                    <Table className='table-hover-animation' responsive>
                        <thead>
                        <tr>
                            <th>Варианты ответа</th>
                            <th>Ваш ответ</th>
                        </tr>
                        </thead>
                        <tbody>
                        {item.answers.map(answers => {
                            return <tr key={answers.answer_id}>
                                <td width={'70%'}>
                                    {answers.answer_text}
                                </td>
                                <td>
                                    {answers.checked ? (answers.answer_true === '1') ? <CheckSquare className={s.success}/> : <XSquare className={s.error}/> : ''}
                                </td>
                            </tr>
                        })}
                        </tbody>
                    </Table>
                )
            }
        ));

    return (
        <ResultComponent
            isReview={props.isReview}
            saveReview={saveReview}
            data={data}
            renderReview={renderReview}
            userAnswers={props.userAnswers}
            percent={props.percent}/>
    );
}

const mapStateToProps = (state) => ({
    userId: state.authPage.userId,
    userAnswers: state.questions.userAnswers,
    percent: state.profilePage.profile.percent,
    isReview: state.profilePage.isReview
});

export default connect(mapStateToProps, {getAllAnswers, saveReview})(ResultContainer);